.slider {
  position: relative; }
  .slider .slick-arrow {
    position: absolute;
    top: 50%;
    z-index: 10;
    font-size: 0;
    width: 66.5px;
    height: 69.94px; }
  .slider .slick-prev {
    background: url("../img/arrow-left.svg") 0 0/100% no-repeat;
    left: 1040px;
    top: 529px;
    background-color: #fff;
    opacity: .2; }
  .slider .slick-next {
    background: url("../img/arrow-right.svg") 0 0/100% no-repeat;
    right: 0;
    top: 529px;
    background-color: #fff;
    opacity: .2; }
  .slider .slick-dots {
    display: flex;
    justify-content: start;
    padding-top: 30px; }
    .slider .slick-dots li {
      list-style: none;
      margin: 0 10px; }
      .slider .slick-dots li button {
        font-size: 0;
        width: 10px;
        height: 10px;
        background-color: #fff;
        border-radius: 50%; }
    .slider .slick-dots .slick-active button {
      background-color: transparent;
      border: 1px solid #fff; }

